body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


main {
  width: 100%;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#quote-box {
  width: 500px;
  padding: 40px;
  border-radius: 10px;
  background-color: red;
}

.row {
  width: 100%;
}

.row > .left {
  float: left;
}

.row > .right {
  float: right;
}

#text {
  text-align: center;
  padding: 10px;
}

#author {
  text-align: right;
  padding: 10px
}

button {
  height: 30px;
}

.button {
  display: inline-block;
  background-color: green;
  width: 30px;
  text-align: center;
  height: 30px;
  line-height: 30px;
  margin-right: 10px;
  border-radius: 5px;
}
